.data {
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

button {
  width: 250px;
  height: 100px;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 3px;
  color: #524f4e;
  background: #fff;
  border-radius: 45px;
  margin: 10px 20px;
  font-family: Montserrat, sans-serif;
  font-size: 40px;
  font-weight: 600;
  line-height: 45px;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
  box-shadow: 0 8px 15px rgba(0, 0, 0, .1);
}

button:hover {
  color: #fff;
  background: #576e65;
  transform: translateY(-7px);
  box-shadow: 0 15px 20px rgba(52, 55, 54, .4);
}

input {
  width: 80px;
  height: 50px;
  padding: 10px;
  font-size: 20px;
}

.boxes {
  text-align: center;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

